This query will take effect for any screen smaller than 760px
and also iPads specifically.
*/
@media (max-width: 1000px) {
  /* Force table to not be like tables anymore */
  table,
  thead,
  tbody,
  th,
  td,
  tr {
    display: block; }
  /* Hide table headers (but not display: none;, for accessibility) */
  thead tr {
    position: absolute;
    clip: rect(1px, 1px, 1px, 1px);
    padding: 0;
    border: 0;
    height: 1px;
    width: 1px;
    overflow: hidden; }
  tr {
    border-bottom: 1px solid #e0ddd3; }
  tr.odd {
    background: #f5f4f1; }
  td {
    /* Behave  like a "row" */
    border: 0;
    padding-left: 50%;
    position: relative; }
  td:before {
    left: 6px;
    padding-right: 10px;
    position: absolute;
    top: 6px;
    white-space: nowrap;
    width: 45%; } }

